.container {
  width: 100%;
  max-width: 1500px;
  margin: auto;
}

.edit-icon-container {
  position: relative !important;
}

.edit-icon-container .edit-container {
  @include desktop {
    visibility: hidden;
  }
}

.edit-icon-container:hover .edit-container {
  @include desktop {
    visibility: visible;
  }
}

#number-input-alert {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;

  .span {
    display: block;
  }
}
