@import "../../../styles/presets";
@import "../../../styles/globals";
@import "../../../styles/hover-effects";
@import "../../../styles/keyframes";

.sauce-finder-container {
  margin: auto;
  transition: 500ms;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  @extend .box-shadow;
  @extend .rounded-border;
  @extend .default-padding;

  @include mobile {
    flex-direction: column;
  }

  .results-container {
    .less-relevant {
      .title {
        font-weight: bold;
      }

      .unhide-button {
        background-color: $primary;
        background-color: $primary;
        padding: 4px 16px;
        color: white;
        font-weight: bold;
        text-decoration: none;
        max-width: min-content;
        white-space: nowrap;
        justify-content: center;
        margin: 8px auto;

        @extend .clickable;
        @extend .rounded-border;
      }
    }
    .sauces-container {
      justify-content: center;
      display: flex;
      flex-wrap: wrap;

      @include mobile {
        margin: auto;
      }

      .sauce-card {
        border-left: 8px solid $primary;
        margin: 12px;
        width: 160px;
        font-size: small;
        animation: bounce-pop 150ms;

        display: flex;
        flex-direction: column;
        justify-content: center;

        @extend .subtle-hover-grow;
        @extend .hover-pointer;
        @extend .default-padding;

        .key {
          font-weight: bold;
          margin-right: 8px;
        }

        .thumbnail {
          width: 90%;
          margin: 8px auto;

          @extend .rounded-border;
        }

        .view-button {
          display: flex;
          align-items: center;
          background-color: $primary;
          padding: 4px 16px;
          color: white;
          font-weight: bold;
          text-decoration: none;
          max-width: min-content;
          white-space: nowrap;
          justify-content: center;
          margin: 8px auto;

          @extend .clickable;
          @extend .rounded-border;

          .link-icon {
            height: 16px;

            @extend .icon;
          }
        }

        @extend .box-shadow;
        @extend .rounded-border;
      }
    }
  }

  .sauce-finder {
    margin: auto;
    width: 90%;
    max-width: 300px;
    margin: 12px;

    .hint-text {
      font-size: large;
      font-weight: bold;
      margin: 24px;
    }

    .or {
      margin: 20px;
    }

    .image-input {
      #image-input {
        display: none;
      }

      .preview-image-container {
        margin: auto;
        padding: 12px;
        max-width: 200px;

        @extend .clickable;

        .preview-image-placeholder {
          @extend .gray-icon;
        }

        .preview-image {
          margin: auto;
          width: 90%;

          @extend .rounded-border;
        }

        @extend .edit-icon-container;
      }
    }

    .url-input {
      display: block;
      text-align: center;
      border-radius: 32px;
      padding: 12px;
      width: 90%;
      border: 1px solid #888;
      margin: 12px auto;
    }

    .search-button {
      width: 90%;
      max-width: 300px;
      background-color: $primary;
      padding: 16px;
      color: white;
      font-weight: bold;
      margin: 24px auto;

      @extend .rounded-border;
      @extend .clickable;
    }
  }
}
